.cart-dropdown-container {
    position: absolute;
    width: 280px;
    height: 360px;
    display: flex;
    flex-direction: column;
    padding: 20px;
    border: 1px solid black;
    background-color: white;
    top: 90px;
    right: 40px;
    z-index: 5;
  }
  
  .cart-dropdown-container .cart-items {
    height: 240px;
    display: flex;
    flex-direction: column;
    overflow: scroll;
  }
  
  .cart-dropdown-container button {
    margin-top: auto;
  }
  