.checkout-item-container {
  width: 100%;
  display: flex;
  min-height: 100px;
  border-bottom: 1px solid darkgrey;
  padding: 15px 0;
  font-size: 20px;
  align-items: center;
}

.checkout-item-container .image-container {
  width: 23%;
  padding-right: 15px;
}

.checkout-item-container .image-container img {
  width: 100%;
  height: 100%;
}

.checkout-item-container .name,
.checkout-item-container .quantity,
.checkout-item-container .price {
  width: 23%;
}

.checkout-item-container .quantity {
  display: flex;
}

.checkout-item-container .quantity .arrow {
  cursor: pointer;
}

.checkout-item-container .quantity .value {
  margin: 0 10px;
}

.checkout-item-container .remove-button {
  padding-left: 12px;
  cursor: pointer;
}


@media only screen and (max-width: 600px) {
  .checkout-item-container {
    flex-direction: column; /* Променете flex-direction на column за малки екрани */
    align-items: flex-start; /* Променете align-items на flex-start за малки екрани */
  }
  
  .checkout-item-container .image-container {
    width: 100%;
    padding-right: 0;
    margin-bottom: 10px;
  }
  
  .checkout-item-container .name,
  .checkout-item-container .quantity,
  .checkout-item-container .price,
  .checkout-item-container .remove-button {
    width: 100%;
    padding-left: 0;
  }
  
  .checkout-container .checkout-header{
    display: none;
  }
}